.cv-app {
    background: rgba(179, 168, 168, 0.336);
    height: 90vh;
    width: 60%;
    margin: 40px auto 0px auto;
    border-radius: 20px;
    box-shadow: 5px 15px 15px rgba(0, 0, 0, 0.404);
    overflow: auto;
}

.title {
    border-bottom: 2px solid black;
    height: 10%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: monospace;
    font-size: 30px;
    color:rgba(39, 35, 35, 0.952);
    background-color: rgba(192, 230, 185, 0.473);
    border-top-left-radius: 20px;
    border-top-right-radius: 20px;
}