label {
    font-family: monospace;
    font-size: 16px;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    gap: 5px;
}

form {
    display: flex;
    flex-direction: column;
    justify-content: center;
}

.formContainer {
    border: 1px solid rgb(253, 246, 246);
    border-radius: 10px;
    width: 90%;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    margin-top: 40px;
}

.textIn {
    width: 200px;
    height: 20px;
    margin-left: 10px;
    outline: none;
    border: none;
    transition: 1s;
}

.textIn:focus {
    border: 1px solid rgba(0, 128, 0, 0.486);
}

.formSubmit {
    margin: 25px auto 0px auto;
    width: 150px;
    height: 35px;
    border: 1px solid black;
    border-radius: 5px;
    background:rgb(250, 248, 248);
    transition: 0.5s;
}

.formSubmit:hover {
    background: rgba(0, 128, 0, 0.281);
}