.test {
    margin: 20px auto 0px auto;
    width: 100%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-bottom: 2px solid rgb(134, 84, 84);
    padding-bottom: 15px;
}

.eduForm {
    width: 100%;
}

.edTitle {
    display: flex;
    justify-content: center;
    font-size: 25px;
}

.eduIn {
    border: 1px solid rgb(253, 246, 246);
    border-radius: 10px;
    width: 90%;
    height: 200px;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    margin-top: 10px;
}

.add {
    margin: 10px auto 0px auto;
    width: 150px;
    height: 35px;
    border: 1px solid black;
    border-radius: 5px;
    background:rgb(250, 248, 248);
    transition: 0.5s;
}

.add:hover {
    background: rgba(0, 128, 0, 0.281);
}

.eduCard {
    width: 90%;
    display: flex;
    flex-direction: row;
}

.eduCardInfo {
    width: 100%;
    display: flex;
    justify-content:space-between;
    margin: 2px 0px 2px 0px;
    border-bottom: 1px solid black;
    padding: 5px 10px 5px 10px;
}

.eduDelete {
    border: none;
    border-radius: 5px;
    background-color: rgba(255, 255, 255, 0);
}

.eduDelete:hover {
    background-color: rgba(255, 0, 0, 0.171);
}