.workContainer {
    border-bottom: 2px solid rgb(134, 84, 84);
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    padding: 10px 0px 15px 0px;
}

.workIn {
    border: 1px solid rgb(253, 246, 246);
    border-radius: 10px;
    width: 90%;
    height: 250px;
    margin-left: auto;
    margin-right: auto;
    display: grid;
    grid-template-columns: 50% 50%;
    grid-template-rows: 50% 50%;
    margin-top: 10px;
}

.workTitle {
    display: flex;
    justify-content: center;
    font-size: 25px;
}

.userWork {
    height: 230px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 5px 0px 10px 0px;
}

.workDes {
    height: 230px;
    padding: 10px 0px 0px 0px;
}

.descripIn {
    border: none;
    outline: none;
    margin-top: 0px;
}

.workCard {
    border-bottom: 1px solid black;
    width: 90%;
    display: flex;
    height: 160px;
}

.workDisplay {
    width: 100%;
    display: flex;
}

.workInfo {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.descDisp {
    width: 50%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}

.desc {
    height: 70%;
    width: 100%;
    overflow: auto;
    text-align: center;
}

.workDel {
    margin-top: 10px;
    color: rgba(255, 0, 0, 0.39);
}

.workDel:hover {
    color: red;
    cursor: pointer;
}