.card {
    border-bottom: 2px solid rgb(134, 84, 84);
    height: 23%;
    display: flex;
    align-items: center;
}

.cardInfo {
    width: 50%;
}

.name {
    width: 100%;
    font-size: 40px;
    display: flex;
    justify-content: center;
    text-align: center;
}

.info {
    width: 100%;
    display: flex;
    justify-content: center;
    font-size: 18px;
    margin-top: 10px;
}

.cardDisp {
    width: 50%;
    height: 100%;
    display: flex;
    justify-content: center;
    flex-direction: column;
}

.image {
    border: 1px solid black;
    height: 75%;
    width: 50%;
    margin: 5px auto 0px auto;
    display: flex;
    justify-content: center;
    align-items: center;
}

.edit {
    width: 100px;
    height: 20px;
    border: none;
    border-radius: 5px;
    transition: 0.5s;
    margin: 8px auto 0px auto;
}

.edit:hover {
    background-color: rgba(88, 81, 81, 0.288);
}